export default {
	data() {
		return {
			dataFormList: [],
			active_index:1,
			imgUrl: this.$imgUrl
		};
	},
	 swiper: function (e) {
		 
	  },
	onLoad:function(){
		this._getLuckCardList()
	},
	methods:{
		is_active(e){
			this.active_index = e.detail.current;
		},
		// 获取列表
		_getLuckCardList () {
			 // 加载动画
			 uni.showLoading({
				 title: '加载中'
			 });
			this.$member.post('Store/get_luck_card_list').then(res => {
				// 关闭加载动画
				uni.hideLoading();
				if (res.data.code == 200) {
					this.dataFormList = res.data.data
				} else {
					 uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
					 })
				}
			}).catch(err => {
				console.log(err)
			})
		},
		toMemberGuardianCardsDetails(edm) {
			uni.navigateTo({
				url: '../../pagesMember/MemberGuardianCardsDetails/MemberGuardianCardsDetails?edm=' + edm
			})
		}
	}
}
